import React, { useEffect, useState } from 'react';
import DateBlock from './DateBlock/DateBlock';
import './SortByDateModal.scss';
import { observer } from 'mobx-react-lite';
import arrowLeft from './img/arrowLeft.svg';
import arrowRight from './img/arrowRight.svg';
import DateStore from '../../../store/DateStore';
import FilterStore from '../../../store/FilterStore';
import close from '../../../img/UI/close.svg';
import { useSorting } from './../../../hooks/useSorting';
import { getTransactions } from '../../../API/transactionsAPI';

const SortByDateModal = observer(({isShow, setShow, classes}) => {
  const applyOtherFilters = useSorting(setShow);
  const [isDisabled, setIsDisabled] = useState(true);


  const sortByDate = async () => {
    let transactions;
    if(DateStore.firstDate < DateStore.secondDate) 
      transactions = await getTransactions(DateStore.firstDate, DateStore.secondDate)
    else 
      transactions = await getTransactions(DateStore.secondDate, DateStore.firstDate)
    // if(DateStore.firstDate < DateStore.secondDate) 
    //   transactions = FilterStore.transactions.filter(transaction =>  DateStore.firstDate <= transaction.date && transaction.date <= DateStore.secondDate)
    // else 
    //   transactions = FilterStore.transactions.filter(transaction =>  DateStore.secondDate <= transaction.date && transaction.date <= DateStore.firstDate)

    FilterStore.setTransactionsByDate(transactions);
  }

  useEffect(() => {
      if(DateStore.firstDate && DateStore.secondDate) setIsDisabled(false);
      else setIsDisabled(true);
  }, [DateStore.firstDate, DateStore.secondDate])

  return (
    (
      <>
        { isShow &&
          <div className={`date ${classes}`}>
            <div className='date__header'>
              <h1 className='date__title'>Filter by calendar</h1>
              <img src={close} alt="close" className='filterByStatus__close' onClick={() => setShow(false)}/>
            </div>
            <div className='date__blocks'>
              <img src={arrowLeft} alt="arrowLeft" className='date__arrow'/>
              <DateBlock isLeft={true}/>
              <DateBlock />
              <img src={arrowRight} alt="arrowRight" className='date__arrow'/>
            </div>
            <div className='date__buttons'>
              <button disabled={isDisabled} className='btn date__btn date__btn-cancel' onClick={() => {
                DateStore.setFirstDate(null);
                DateStore.setSecondDate(null);
                FilterStore.setFilteredByDate(false);
                applyOtherFilters();
              }}>Deselect all</button>
              <button disabled={isDisabled} className='btn date__btn' onClick={async () => {
                FilterStore.setFilteredByDate(true);
                await sortByDate();
                applyOtherFilters();
              }}>Apply</button>
            </div>
          </div>
        }
      </>
    )
  );
});

export default SortByDateModal;