import { makeAutoObservable } from "mobx";
import { getMonthBeforeDate } from '../utils/dates';

const [today, monthLater] = getMonthBeforeDate(1);

class DateStore {
  constructor() {
    this.daysLeft = [];
    this.monthLeft = new Date(monthLater).toLocaleString('en-US', {month: 'long'});
    this.yearLeft = '2022';

    this.daysRight = [];
    this.monthRight = new Date().toLocaleString('en-US', {month: 'long'});
    this.yearRight = '2022';

    this.firstDate = null;
    this.secondDate = null;

    makeAutoObservable(this);
  }

  setFirstDate(firstDate) {
    this.firstDate = firstDate;
  }
  setSecondDate(secondDate) {
    this.secondDate = secondDate;
  }
  
  setDaysLeft(daysLeft) {
    this.daysLeft = daysLeft;
  }
  setMonthLeft(monthLeft) {
    this.monthLeft = monthLeft;
  }
  setYearLeft(yearLeft) {
    this.yearLeft = yearLeft;
  }

  setDaysRight(daysRight) {
    this.daysRight = daysRight;
  }
  setMonthRight(monthRight) {
    this.monthRight = monthRight;
  }
  setYearRight(yearRight) {
    this.yearRight = yearRight;
  }
}

export default new DateStore();

