import React, { useEffect } from 'react';
import './SortByCategory.scss';
import translations from './img/translations.svg';
import topup from './img/topup.svg';
import withdraw from './img/withdraw.svg';
import other from './img/other.svg';
import close from '../../../img/UI/close.svg';
import FilterStore from '../../../store/FilterStore';
import { observer } from 'mobx-react-lite';
import { useSorting } from './../../../hooks/useSorting';
import { useDebounce } from './../../../hooks/useDebounce';
import userStore from '../../../store/UserStore';

const SortByCategory = observer(({isShow, setShow, onClick}) => {
  const toggleCheckedStatusOfInput = (index) => {
    let newFiltersArr = JSON.parse(JSON.stringify(FilterStore.categoryFilters));
    newFiltersArr[index].state = !newFiltersArr[index].state;
    FilterStore.setCategoryFilters(newFiltersArr)
  }
  
  const filters = [
    {icon: translations, title: 'Translations', checked: FilterStore.categoryFilters[1].state, 
      onChange: () => toggleCheckedStatusOfInput(1)},
    {icon: topup, title: 'Top up', checked: FilterStore.categoryFilters[0].state, 
      onChange: () => toggleCheckedStatusOfInput(0)},
    {icon: withdraw, title: 'Withdraw', checked: FilterStore.categoryFilters[2].state, 
      onChange: () => toggleCheckedStatusOfInput(2)},
    {icon: other, title: 'Other', checked: FilterStore.categoryFilters[3].state, 
      onChange: () => toggleCheckedStatusOfInput(3)},
  ]

  const sortCategory = useSorting(setShow);
  const sortCategoryDebounce = useDebounce(sortCategory, 500);

  useEffect(() => {
    if(FilterStore.categoryFilters.some(categoryFilter => categoryFilter.state === true)) {
      FilterStore.setFilteredByCategory(true);
      sortCategoryDebounce();  
    } 
    else {
      if(FilterStore.filteredByCategory) {
        FilterStore.setFilteredByCategory(false);
        sortCategoryDebounce();  
      }
    }
  }, [FilterStore.categoryFilters]);

  return (
    <>
      { isShow &&
        <div className='sortByCategory' onClick={onClick}>
          <div className='sortByCategory__header'>
            <h1 className='sortByCategory__title'>Sort by category</h1>
            <img src={close} alt="close" onClick={() => setShow(false)}/>
          </div>
          <div className='sortByCategory__variants'>
            {
              filters.map((filter) => 
              <div className='sortByCategory__variant' key={filter.title}>
                <input type="checkbox" className='sortByCategory__input' 
                  checked={filter.checked}
                  onChange={filter.onChange}/>
                <img src={filter.icon} alt={`${filter.icon}`} />
                {filter.title} 
              </div>
              )
            }
          </div>
          <div className='sortByCategory__buttons'>
            <button className='btn sortByCategory__apply' onClick={() => setShow(false)}>Apply</button>
            <button className='btn sortByCategory__clear' onClick={() => FilterStore.setCategoryFilters( // set state of filters to false
              FilterStore.categoryFilters.map(filter => {
                filter.state = false;
                return filter;
              })
            )}>Clear all filters</button>
          </div>
        </div>
      }
    </>
  );
});

export default SortByCategory;