import React from 'react';
import './Promo.scss';
import promo1 from '../../img/promo/promo.png';
import promo2 from '../../img/promo/promo2.png';
import promo3 from '../../img/promo/promo3.png';
import { useState } from 'react';


const Promo = React.memo(({classes = '', isSlider = false, title = '', subtitle = ''}) => {
  const promoImages = [promo1, promo2, promo3];
  const [slideIndex, setSlideIndex] = useState(0);

  const nextSlide = () => {
    if (slideIndex !== promoImages.length - 1) setSlideIndex(slideIndex + 1);
    else setSlideIndex(0);
  }

  return (
    <div className={`promo ${classes}`}>
      <div className='promo__content'>
        <h1 className='promo__title'>{title}</h1>
        <p className='promo__subtitle'>{subtitle}</p>
        <button className='promo__btn btn' >Read more</button>
      </div>
      {isSlider &&
        <>
          <div className='promo__slider'>
            {
              promoImages.map((image, index) => 
                <div className={`promo__slide ${slideIndex === index ? 'promo__slide-active' : ''}`} key={image} >
                  <img src={image} alt={image} />
                </div>
              )
            }
          </div>
          <div className='promo__next' onClick={nextSlide}>
            <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M1.29055 9.88047L5.17055 6.00047L1.29055 2.12047C0.900547 1.73047 0.900547 1.10047 1.29055 0.710469C1.68055 0.320469 2.31055 0.320469 2.70055 0.710469L7.29055 5.30047C7.68055 5.69047 7.68055 6.32047 7.29055 6.71047L2.70055 11.3005C2.31055 11.6905 1.68055 11.6905 1.29055 11.3005C0.910547 10.9105 0.900547 10.2705 1.29055 9.88047Z" fill="#1B2B41" fillOpacity="0.72"/>
            </svg>
          </div>
          <div className='promo__pagination'>
            {
              promoImages.map((image, index) => 
                <div 
                  className={`pagination__item ${slideIndex === index ? 'pagination__item-active' : ''}`} 
                  key={image}
                  onClick={() => setSlideIndex(index)}>
                </div>
              )
            }
          </div>
        </>
      }   
    </div>
  );
});

export default Promo;