import React from 'react';
import './SearchInput.scss';
import search from '../../img/UI/search.svg';

const SearchInput = ({classesInput = '', classesContainer = '', onChange, ...props}) => {
  return (
    <div className={`search ${classesContainer}`}>
      <img src={search} alt='search__icon' className='search__icon' />
      <input 
        type='text' 
        className={`search__input ${classesInput}`}
        placeholder='Search'
        onChange={(e) => onChange(e.target.value)}
        {...props}/>
    </div>
  );
};

export default SearchInput;