import React, { useEffect } from 'react';
import { useState } from 'react';
import { getDaysInMonth } from '../../../../utils/dates';
import DateStore from '../../../../store/DateStore';
import { observer } from 'mobx-react-lite';

const DateBlock = observer(({isLeft}) => {
  const monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"];

  const weekdays = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT' ];
  
  const [years, setYears] = useState([]);

  useEffect(() => {
    const today = new Date();
    let yearsLet = []
    for(let i = 0; i <= 3; i++) yearsLet.push(today.toLocaleString('en-us',{year:'numeric'}) - i);
    setYears(yearsLet);
  }, []);

  useEffect(() => {
    if(!isLeft) {
      const date = new Date(+DateStore.yearRight, monthNames.indexOf(DateStore.monthRight), 1);
      const days = getDaysInMonth(date);
      DateStore.setDaysRight(days);
    }
  }, [DateStore.monthRight, DateStore.yearRight])

  useEffect(() => {
    if(isLeft) {
      const date = new Date(+DateStore.yearLeft, monthNames.indexOf(DateStore.monthLeft), 1);
      const days = getDaysInMonth(date);
      DateStore.setDaysLeft(days);
    }
  }, [DateStore.monthLeft, DateStore.yearLeft])

  const handleClickOnDate = (e) => {
    const dayId = e.target.id;
  
    let day;
    if(isLeft) day = DateStore.daysLeft.at(dayId);
    else day = DateStore.daysRight.at(dayId);

    if (!DateStore.firstDate  || !DateStore.secondDate ) {
      e.target.classList.toggle('date__day-active')
      if (DateStore.firstDate === day.date) DateStore.setFirstDate(null)
      else if (DateStore.secondDate === day.date) DateStore.setSecondDate(null)
      else if (!DateStore.firstDate) DateStore.setFirstDate(day.date)
      else if (DateStore.firstDate !== DateStore.secondDate && !DateStore.secondDate) DateStore.setSecondDate(day.date)
    }
    else if (DateStore.firstDate === day.date) {
      e.target.classList.toggle('date__day-active')
      DateStore.setFirstDate(null)
    }
    else if (DateStore.secondDate === day.date) {
      e.target.classList.toggle('date__day-active')
      DateStore.setSecondDate(null)
    }
    else DateStore.setSecondDate(day.date)
    
  }

  return (
    <div className='date__block'>
      <div className='block__header'>
        {isLeft 
         ? 
          <>
            <select className='block__date' 
              onChange={(e) => DateStore.setMonthLeft(e.target.value)} 
              defaultValue={DateStore.monthLeft}>
              {monthNames.map((y) => <option key={y} value={y}>{y}</option>)}
            </select>
            <select className='block__date' 
              onChange={(e) => DateStore.setYearLeft(e.target.value)}
              defaultValue={DateStore.yearsLeft}>
              {years.map((y) => <option key={y} value={y}>{y}</option>)}
            </select>
          </>
         :
          <>
            <select className='block__date' 
              onChange={(e) => DateStore.setMonthRight(e.target.value)}
              defaultValue={DateStore.monthRight}>
              {monthNames.map((y) => <option key={y} value={y}>{y}</option>)}
            </select>
            <select className='block__date' 
              onChange={(e) => DateStore.setYearRight(e.target.value)}
              defaultValue={DateStore.yearsRight}>
              {years.map((y) => <option key={y} value={y}>{y}</option>)}
            </select>
          </>
        }
      </div>
      <div className='block__weekdays'>
        { weekdays.map(weekday => <span className='block__weekday' key={weekday}>{weekday}</span>) }
      </div>
      <div className='block__days'>
        {isLeft 
          ? DateStore?.daysLeft.map(day => <div 
              className={`block__day 
              ${day.this === true ? 'block__day-this' : ''} 
              ${day.date === DateStore.firstDate || day.date === DateStore.secondDate ? 'block__day-active' : ''}`}
              key={day.id} id={day.id}
              onClick={handleClickOnDate}>{day.day}</div>)
          : DateStore?.daysRight.map(day => <div 
              className={`block__day 
              ${day.this === true ? 'block__day-this' : ''} 
              ${day.date === DateStore.firstDate || day.date === DateStore.secondDate ? 'block__day-active' : ''}`}
              key={day.id} id={day.id}
              onClick={handleClickOnDate}>{day.day}</div>)
        }
      </div>
    </div>
  );
});

export default DateBlock;