import FilterStore from "../store/FilterStore";
import userStore from "../store/UserStore";


export const useSorting = (setShow) => {
  return async () => {
    if(setShow) setShow(false);
    FilterStore.setSorting(true);

    // if transactions sorted by date then set basic transactions list by this sorted date transactions list
    if(FilterStore.filteredByDate) FilterStore.setTransactions(FilterStore.transactionsByDate);
    else FilterStore.setTransactions(userStore.transactions);

    if(FilterStore.filteredByCategory) {
      let filteredByCategoryTransactions = [];
      const filterTypes = FilterStore.categoryFilters.filter((categoryFilter) => categoryFilter.state === true)
                      .map((categoryFilter) => categoryFilter.type); // firstly get all checked filter types
      filteredByCategoryTransactions = FilterStore.transactions.filter(transaction =>
          filterTypes.some(type => type === transaction.type)); // then check if it one of checked types == transaction.type
      FilterStore.setTransactions(filteredByCategoryTransactions);
    }

    if(FilterStore.filterByAmount) {
      FilterStore.setTransactions(
        FilterStore.transactions
        .filter(transaction => +FilterStore.from <= +transaction.value && +transaction.value <= +FilterStore.to))
    }

    if(FilterStore.filteredBySearch) {
      if(FilterStore.searchValue === null ||FilterStore.searchValue === '') FilterStore.setFilteredBySearch(false);
      FilterStore.setTransactions(FilterStore.transactions.filter((transaction) => transaction.value.startsWith(FilterStore.searchValue)));
    }

    // if no transactions then set alert by changing state of isResults on false
    if(FilterStore.transactions.length === 0) FilterStore.setIsResults(false);
    else FilterStore.setIsResults(true);
    FilterStore.setSorting(false);
  }
}