export const getMonthBeforeDate = (monthCount, date = new Date()) => {
  let monthLater = new Date();
  monthLater.setMonth(date.getMonth() - monthCount);
  return [date.toLocaleDateString('en-CA'), monthLater.toLocaleDateString('en-CA')]
}

export const getDateName = (date) => {
  if(!date) return 'Today';
  const today = new Date(new Date().toLocaleDateString('en-CA'));
  const dateObj = new Date(date);
  const diffTime = Math.abs(today - dateObj);
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24)); 

  if(diffDays === 0) return 'Today';    
  if(diffDays === 1) return 'Yesterday';
  if(diffDays > 1) {
    const day =  dateObj.toLocaleString('en-us',{day: 'numeric'});
    const month =  dateObj.toLocaleString('en-us',{month:'long'});
    const year = dateObj.toLocaleString('en-us',{year:'numeric'});
    return day + " " + month + ", " + year;
  }
}

export const getDaysInMonth = (date) => {
  let monthBefore = new Date(date.getFullYear(), date.getMonth(), 0);
  let monthBeforeDay = monthBefore.getDate();

  let firstDay = new Date(date.getFullYear(), date.getMonth(), 1)
  let firstDayNum = new Date(date.getFullYear(), date.getMonth(), 1).getDate();
  let firstDayName = firstDay.toLocaleString('en-us',{ weekday: 'short'}).toUpperCase();

  let lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);
  let lastDayNum = new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate();
  let lastDayName = lastDay.toLocaleString('en-us',{ weekday: 'short'}).toUpperCase();

  let monthAfter = new Date(date.getFullYear(), date.getMonth() + 1, 1);
  let monthAfterDay = monthAfter.getDate();

  const weekdays = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT' ];
  const startIndex =  weekdays.indexOf(firstDayName);

  const daysLet = []
  let dayNum = 1;
  for(let i = 0; i < 35; i++) {
    if(i < startIndex) daysLet.push({day: monthBeforeDay - startIndex + i + 1, id: i, 
      date: new Date(monthBefore.getFullYear(), monthBefore.getMonth(),
      monthBeforeDay - startIndex + i + 1).toLocaleDateString('en-CA'),
      this: false})
    else if(i >= startIndex && dayNum <= lastDayNum) daysLet.push({day: dayNum++, id: i, 
      date: new Date(firstDay.getFullYear(), firstDay.getMonth(),
      dayNum - 1).toLocaleDateString('en-CA'),
      this: true})
    else daysLet.push({day: monthAfterDay++, id: i, 
      date: new Date(monthAfter.getFullYear(), monthAfter.getMonth(),
      monthAfterDay - 1).toLocaleDateString('en-CA'),
      this: false})
  }
  return daysLet;
}